.list-enter {
  transform: translateX(-100%);
}

.list-enter-active {
  transform: translateX(0);
  transition: transform 250ms linear;
}

.list-exit {
  transform: translateX(0);
}

.list-exit-active {
  transform: translateX(100%);
  transition: transform 250ms linear;
}

.contact_list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

.phone_Item {
  position: relative;
  padding: 0 10px 0 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
  box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.75);
  line-height: 30px;
}

.phone_number {
  position: absolute;
  left: 200px;
}

.phone_Item button {
  width: 20px;
  height: 20px;
  border: none;
  cursor: pointer;
}

.contact_list li button:hover {
  background-color: red;
  border-radius: 50%;
}
